"use client";

import { useEffect } from "react";
import { usePathname, useSearchParams } from "next/navigation";
import { v4 as uuidv4 } from "uuid";
import { captureUTMParameters, captureVisitData } from "@/utils/tracking/utm";
import { createClient } from "@/utils/supabase/client";

const ANONYMOUS_ID_COOKIE = "arti_anonymous_id";
const COOKIE_EXPIRY = 365; // 1 year

export default function TrackingProvider({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();
  const searchParams = useSearchParams();
  const supabase = createClient();

  // Generate or retrieve anonymous ID for the visitor
  useEffect(() => {
    const getOrCreateAnonymousId = async () => {
      try {
        // Try to get the cookie from document.cookie first (client-side)
        const cookies = document.cookie.split(";");
        const anonymousIdCookie = cookies.find((cookie) =>
          cookie.trim().startsWith(`${ANONYMOUS_ID_COOKIE}=`)
        );

        let anonymousId = anonymousIdCookie
          ? anonymousIdCookie.split("=")[1]
          : null;

        if (!anonymousId) {
          anonymousId = uuidv4();
          // Set cookie with proper options
          document.cookie = `${ANONYMOUS_ID_COOKIE}=${anonymousId}; path=/; max-age=${
            COOKIE_EXPIRY * 24 * 60 * 60
          }; sameSite=lax${
            process.env.NODE_ENV === "production" ? "; secure" : ""
          }`;
        }
      } catch (error) {
        console.error("Error handling anonymous ID:", error);
      }
    };

    getOrCreateAnonymousId();
  }, []);

  // Capture UTM parameters and visit data when URL changes
  useEffect(() => {
    if (!pathname) return;

    const handleTracking = async () => {
      try {
        // Get the full URL including query parameters
        const url = window.location.href;

        // Capture UTM parameters from URL
        const utmParams = await captureUTMParameters(url);

        // Capture visit data if this is a new session
        await captureVisitData();

        // If UTM parameters exist, store them in the database
        if (
          Object.values(utmParams).some((value) => value) ||
          pathname === "/"
        ) {
          const cookies = document.cookie.split(";");
          const anonymousIdCookie = cookies.find((cookie) =>
            cookie.trim().startsWith(`${ANONYMOUS_ID_COOKIE}=`)
          );
          const anonymousId = anonymousIdCookie
            ? anonymousIdCookie.split("=")[1]
            : null;

          if (anonymousId) {
            await saveTrackingData(anonymousId, utmParams);
          }
        }
      } catch (error) {
        console.error("Error in TrackingProvider:", error);
      }
    };

    handleTracking();
  }, [pathname, searchParams]);

  // Function to save tracking data to the database
  const saveTrackingData = async (anonymousId: string, utmParams: any) => {
    try {
      // Check if user is authenticated
      const {
        data: { user },
      } = await supabase.auth.getUser();

      // Build tracking data object
      const trackingData: Record<string, any> = {
        anonymous_id: anonymousId,
        user_id: user?.id,
        utm_source: utmParams.utm_source,
        utm_medium: utmParams.utm_medium,
        utm_campaign: utmParams.utm_campaign,
        utm_term: utmParams.utm_term,
        utm_content: utmParams.utm_content,
        referrer: document.referrer || null,
        landing_page: window.location.href,
        first_visit: new Date().toISOString(),
      };

      // Remove undefined values
      Object.keys(trackingData).forEach((key) => {
        if (trackingData[key] === undefined) {
          delete trackingData[key];
        }
      });

      // Save to database
      const { error } = await supabase
        .from("tracking_data")
        .insert(trackingData);

      if (error && !error.message.includes("duplicate")) {
        console.error("Error saving tracking data:", error);
      }
    } catch (error) {
      console.error("Error saving tracking data:", error);
    }
  };

  return <>{children}</>;
}
