// src/utils/tracking/utm.ts
import { cookies } from "next/headers";

export interface UTMParams {
  utm_source?: string;
  utm_medium?: string;
  utm_campaign?: string;
  utm_term?: string;
  utm_content?: string;
  referrer?: string;
  landing_page?: string;
  first_visit?: string;
}

// Cookie settings
const COOKIE_OPTIONS = {
  expires: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000), // 30 days
  path: "/",
  sameSite: "lax" as const,
  secure: process.env.NODE_ENV === "production",
};

// Cookie names
export const UTM_COOKIE_NAME = "arti_utm_data";
export const VISIT_COOKIE_NAME = "arti_visit_data";

/**
 * Capture UTM parameters from URL and store in cookies
 */
export async function captureUTMParameters(url?: string): Promise<UTMParams> {
  // Use provided URL or current window URL
  const currentUrl =
    url || (typeof window !== "undefined" ? window.location.href : "");
  if (!currentUrl) return {};

  try {
    const urlObj = new URL(currentUrl);
    const searchParams = urlObj.searchParams;

    // Extract UTM parameters
    const utmParams: UTMParams = {
      utm_source: searchParams.get("utm_source") || undefined,
      utm_medium: searchParams.get("utm_medium") || undefined,
      utm_campaign: searchParams.get("utm_campaign") || undefined,
      utm_term: searchParams.get("utm_term") || undefined,
      utm_content: searchParams.get("utm_content") || undefined,
    };

    // Only save if at least one UTM parameter is present
    if (Object.values(utmParams).some((value) => value)) {
      const existingData = await getUTMDataFromCookie();

      // Only save new UTM data if we don't already have these parameters stored
      // or if we're in a new session
      if (!existingData || !existingData.utm_source) {
        await saveUTMDataToCookie(utmParams);
      }
    }

    return utmParams;
  } catch (error) {
    console.error("Error capturing UTM parameters:", error);
    return {};
  }
}

/**
 * Store visit data in cookies
 */
export async function captureVisitData(): Promise<void> {
  try {
    if (typeof window === "undefined") return;

    const cookieStore = await cookies();
    const existingData = cookieStore.get(VISIT_COOKIE_NAME);
    if (existingData) return; // Only track first visit

    const visitData = {
      first_visit: new Date().toISOString(),
      referrer: document.referrer || undefined,
      landing_page: window.location.href,
    };

    cookieStore.set(
      VISIT_COOKIE_NAME,
      JSON.stringify(visitData),
      COOKIE_OPTIONS
    );
  } catch (error) {
    console.error("Error capturing visit data:", error);
  }
}

/**
 * Save UTM data to cookie
 */
export async function saveUTMDataToCookie(data: UTMParams): Promise<void> {
  if (typeof window === "undefined") return;

  // Don't save empty objects
  if (!Object.values(data).some((value) => value)) return;

  try {
    const cookieStore = await cookies();
    cookieStore.set(UTM_COOKIE_NAME, JSON.stringify(data), COOKIE_OPTIONS);
  } catch (error) {
    console.error("Error saving UTM data to cookie:", error);
  }
}

/**
 * Get UTM data from cookie
 */
export async function getUTMDataFromCookie(): Promise<UTMParams | null> {
  if (typeof window === "undefined") return null;

  try {
    const cookieStore = await cookies();
    const utmData = cookieStore.get(UTM_COOKIE_NAME);
    return utmData ? JSON.parse(utmData.value) : null;
  } catch (error) {
    console.error("Error getting UTM data from cookie:", error);
    return null;
  }
}

/**
 * Get visit data from cookie
 */
export async function getVisitDataFromCookie(): Promise<{
  first_visit?: string;
  referrer?: string;
  landing_page?: string;
} | null> {
  if (typeof window === "undefined") return null;

  try {
    const cookieStore = await cookies();
    const visitData = cookieStore.get(VISIT_COOKIE_NAME);
    return visitData ? JSON.parse(visitData.value) : null;
  } catch (error) {
    console.error("Error getting visit data from cookie:", error);
    return null;
  }
}

/**
 * Get all tracking data (UTM + visit data)
 */
export async function getAllTrackingData(): Promise<UTMParams> {
  const utmData = (await getUTMDataFromCookie()) || {};
  const visitData = (await getVisitDataFromCookie()) || {};

  return {
    ...utmData,
    ...visitData,
  };
}

/**
 * Clear all tracking cookies
 */
export async function clearTrackingCookies(): Promise<void> {
  if (typeof window === "undefined") return;

  try {
    const cookieStore = await cookies();
    cookieStore.delete(UTM_COOKIE_NAME);
    cookieStore.delete(VISIT_COOKIE_NAME);
  } catch (error) {
    console.error("Error clearing tracking cookies:", error);
  }
}
